@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}html,body{font-size:10px;margin:0;padding:0;font-family:"Noto Sans JP",sans-serif;background-color:#9bc1bc}.app{display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin:5rem auto;padding:3rem}.app h1{margin-bottom:1rem;font-size:4rem;letter-spacing:.2rem;font-weight:bold;color:#f5f5f5;text-shadow:-1px -1px 0 #ed6a5a,2px -1px 0 #ed6a5a,-1px 1px 0 #ed6a5a,2px 2px 0 #ed6a5a;text-align:center}.app #quote-box{display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;margin:auto;width:95%;background-color:#f5f5f5;color:#000;border:.7rem solid #ed6a5a;border-radius:3rem;padding:2rem;font-size:1.8rem}@media(min-width: 992px){.app #quote-box{width:60%}}.app #quote-box .quote{width:90%;margin:auto}.app #quote-box .quote .text{font-size:1.8rem;letter-spacing:.03rem;font-weight:normal;text-align:start}.app #quote-box .quote .author{font-size:1.4rem;font-weight:lighter;text-align:start}.app #quote-box .buttons{display:-webkit-flex;display:flex;-webkit-flex-flow:row;flex-flow:row;-webkit-justify-content:space-evenly;justify-content:space-evenly;margin:5rem auto 0 auto;font-size:1.4rem}.app #quote-box .buttons button{margin:0 .2rem;padding:.5rem 1rem;text-decoration:none;border:none;border-radius:.5rem;background:#36c9c6}.app #quote-box .buttons button#new-quote{color:#fff}.app #quote-box .buttons button a{text-decoration:none;color:#fff}.app #quote-box .buttons button:hover{background-color:#ed6a5a}
